.contact-section {
	background: url("./images/14.svg") no-repeat;
	background-size: cover;
	width: 100%;
	min-height: 900px;
	left: 0px;
	z-index: -2;
	margin-bottom: 10px;
}

.contact-container {
	display: flex;
	flex-direction: row;
	width: 70%;
	justify-content: space-between;
	margin-top: 110px;

	align-items: center;
	margin-left: 15%;
}

.contact-title {
	padding-top: 280px;
	margin-left: 100px;
}

.contact {
	width: 110px;
	height: 110px;
	text-decoration: none;
}

.contact p {
	margin-top: 20px;
	text-align: center;
	color: rgb(39, 39, 39);
}

.icon {
	width: 100%;
	height: 100%;
	cursor: pointer;
}

@media only screen and (max-width: 1200px) {
	.contact-title {
		padding-top: 250px;
		margin-left: 30px;
		text-align: left;
	}
	.contact {
		width: 60px;
		height: 60px;
		margin-top: 90px;
		margin-bottom: 10px;
	}
}

@media only screen and (max-width: 690px) {
	.contact-section {
		min-height: 800px;
	}

	.contact-container {
		flex-wrap: wrap;
		justify-content: center;
		width: 80%;
		margin-top: 0px;
		margin-left: 8%;
	}

	.contact-title {
		padding-top: 220px;
		margin-left: 30px;
		text-align: left;
	}

	.contact {
		width: 60px;
		height: 60px;
		margin-top: 0px;
		margin-left: 11%;
		margin-bottom: 100px;
	}

	.contact p {
		margin-top: 10px;
	}
}
