.about-container {
	display: flex;
	flex-direction: row;
	width: 80%;
	justify-content: space-around;
	align-items: center;
	margin-top: 6%;
	margin-left: 6%;
}

.about-pic {
	width: 275px;
	height: 275px;
	flex-basis: 50%;

	text-align: center;
}

.about-pic img {
	max-width: 100%;
	height: 100%;
	border-radius: 50%;
}

.about-text {
	flex-basis: 70%;
	/* background-color: azure; */
}

.about-text h1 {
	font-size: 60px;
	font-weight: 300;
	text-align: center;
	margin-bottom: 10px;
}

.about-text h2 {
	font-size: 40px;
	font-weight: 200;
	text-align: center;
	margin-bottom: 30px;
}

@media only screen and (max-width: 1600px) {
	.about-text h1 {
		font-size: 55px;
	}
}

@media only screen and (max-width: 1200px) {
	.about-container {
		flex-direction: column;
		align-items: center;
		margin-top: 13%;
		margin-left: 10%;
	}

	.about-pic {
		margin-bottom: 50px;
	}

	.about-text {
		text-align: justify;
	}

	.about-text h1 {
		font-size: 50px;
	}

	.about-text h2 {
		font-size: 35px;
	}
}

@media only screen and (max-width: 690px) {
	.about-container {
