.project-container {
	width: 1250px !important;
	min-height: 500px;
	background: #fefcfc;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 10px;
	color: rgb(19, 18, 18);
	display: flex;
	flex-direction: row;
	transition: all 0.6s ease-in-out;
}

.project-container:hover {
	transform: scale(1.02);
}

.project-img {
	flex-basis: 550px;
	border-radius: 10px 0px 0px 10px;
	background-color: rgba(127, 255, 212, 0);
}

.pimg {
	width: 100%;
	height: 100%;

	border-radius: 10px 0px 0px 10px;
	background-color: rgba(127, 255, 212, 0);
}

.project-text {
	flex-basis: 60%;
	border-radius: 0px 10px 10px 0px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.project-text h1 {
	font-weight: 300;
	font-size: 35px;
	text-align: center;
	margin-top: 35px;
}

.description {
	width: 72%;
	text-align: justify;
	margin-left: 100px;
	margin-top: 50px;
	font-size: 16px;
	font-weight: 200;
}

.tags {
	margin-top: 50px;
	text-align: center;
	font-size: 18px;
	color: rgb(168, 168, 168);
}

.button-container {
	padding: 40px 40px 40px 40px;
	margin-top: 7px;
	display: flex;
	justify-content: flex-end;
}

.button {
	padding: 20px 40px;
	background: #ffffff;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.133);
	border-radius: 30px;
	text-decoration: none;
	margin-right: 30px;
	transition: all 0.3s ease-in-out;
	color: rgb(34, 33, 34);
	font-size: 20px;
}

.button:hover {
	transform: scale(1.1);
	color: rgb(111, 87, 134);
}

@media only screen and (max-width: 1400px) {
	.project-container {
		width: 1180px !important;
		min-height: 550px;
	}
}

@media only screen and (max-width: 1340px) {
	.project-container {
		min-height: 455px;
		max-height: none;
	}
}

@media only screen and (max-width: 1024px) {
