.skills-main-container {
	background: url("./images/14.svg") no-repeat;
	background-size: cover;
	width: 100%;
	min-height: 900px;
	z-index: -2;
	margin-top: 10px;
	/* 110px */
}

.skill-container {
	width: 85%;
	min-height: 400px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 60px;
	justify-content: space-around;
	align-items: center;
	margin-top: 90px;
	margin-left: 7%;
	/* top 90px */
}

.skill {
	width: 110px;
	height: 110px;
}

.skill-title {
	padding: 250px 0px 0px 0px;
	margin-left: 100px;
}

.react {
	width: 115px !important;
}

.skill p {
	text-align: center;
	margin-top: 10px;
	color: rgb(102, 101, 101);
	font-size: 19px;
	font-weight: 400;
}

.logo {
	width: 100%;
	height: 100%;
	transition: all 0.4s ease-in-out;
}

.logo:hover {
	transform: scale(1.1);
}

@media only screen and (max-width: 1200px) {
	.skills-main-container {
		min-width: 100%;
		min-height: 750px;
		margin-top: 20px;
	}

	.skill-container {
		width: 85%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 40px;
		margin-left: 4%;
	}

	.skill {
		width: 80px;
		height: 80px;
		margin-bottom: 100px;
		margin-left: 10%;
	}

	.skill-title {
		padding-top: 190px;
		text-align: left;
		margin-left: 45px;
		margin-bottom: 60px;
		font-size: 40px;
	}

	.react {
		width: 85px !important;
	}

	.skill p {
		margin-top: 7px;
		font-size: 18px;
	}
}

@media only screen and (max-width: 690px) {
	.skills-main-container {
		min-width: 100%;
		min-height: 750px;
		margin-top: 20px;
	}

	.skill-container {
		width: 85%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 40px;
		margin-left: 4%;
	}

	.skill {
		width: 50px;
		height: 50px;
		margin-bottom: 70px;
		margin-left: 10%;
	}

	.skill-title {
		padding-top: 190px;
		text-align: left;
		margin-left: 30px;
		margin-bottom: 50px;
		font-size: 35px;
	}

	.react {
		width: 55px !important;
	}

	.skill p {
		margin-top: 7px;
		font-size: 12px;
	}
}
